h1 {
  width: 100%;
  background-color: #ff5252;
  text-align: center;
  padding: 5px;
  color: #fff;
}

h4 {
  color: #ff5252;
}

p {
  font-size: 1.25em;
  color: #fff;
}

.blank-row {
  padding-top: 100px !important;
}

.row-gap {
  margin-top: 70px;
}

.btn-container {
  position: fixed;
  padding: 10px;
  z-index: 1;
}
.btn-circle {
  padding: 0;
  border-radius: 50% !important;
  background-color: #f8f9fa !important;
}
.btn-circle-xl {
  width: 65px;
  height: 65px;
}
.btn-clicked {
  width: 75px;
  height: 75px;
  border-radius: 50% !important;
  background-color: #ff5252;
}

.img-company {
  width: 150px;
  height: 150px;
  border-radius: 50% !important;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.img-company-col {
  max-width: 180px !important;
}

.job-title-col {
  text-align: right !important;
}

.job-description-col {
  min-width: 360px !important;
}

.job-technologies {
  text-align: center !important;
  max-width: 1000px !important;
  width: 1000px;
  background-color: #ff5252;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  margin-bottom: 50px;
  padding: 2px;
  border-radius: 5px !important;
  color: #fff;
}

.github,
.linkedin {
  font-size: 1.75em !important;
  margin: 15px !important;
}

.github-linkedin-container {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.about-me-content {
  color: #fff;
  text-align: center;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 50px;
}
